/* Sidebar */
.sidebar .widget {
  border: 1px solid #e0e4e6;
  border-radius: 5px;
  padding: 25px;
  position: relative;
  margin-bottom: 30px;
}

.sidebar .widget .widget-title {
  text-transform: capitalize;
  margin-bottom: 20px;
}
.categories {
  padding-bottom: 40px;
}

.categories li a {
  color: #83868c;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  border-bottom: 1px solid #e0e4e6;
  padding: 7px 0;
}

.categories li a span {
  display: inline-block;
  margin-left: auto;
}

.categories li:last-child a {
  border-bottom: 0;
}
.widget_search {
  padding: 0 !important;
  border: none !important;
}

.widget_search form {
  position: relative;
}

.widget_search form i {
  position: absolute;
  top: 50%;
  right: 15px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}

.widget_search input {
  background-color: none;
  border: 1px solid #e0e4e6;
  border-radius: 5px;
  padding: 15px 40px 15px 25px;
