/*# sourceMappingURL=bootstrap.min.css.map */
/*
    Theme Name: Spa
    Theme URI: https://themeies.com
    Author: Themeies
    Author URI: https://themeies.com
    Version: 1.0
    Description: Spa is a Landing HTML5 Template.
*/
/*
    Table of Context
    -------------------------------
    01.
*/
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  color: #6c6c6f;
  background-color: #fff;
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  font-size: 15px;
  line-height: 27px;
}

a {
  -webkit-transition: all 300ms ease-in-out;
  transition: all 300ms ease-in-out;
}

a:hover, a:focus {
  text-decoration: none;
  outline: 0px;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Rufina', serif;
  font-weight: 700;
}

img {
  display: block;
  width: 100%;
  height: auto;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

figure {
  margin: 0;
}

section {
  display: block;
  position: relative;
  padding: 50px 0;
  width: 100%;
}

@media screen and (min-width: 768px) {
  .nopadd-ltf {
    padding-left: 0;
  }
  .nopadd-rth {
    padding-right: 0;
  }
  .nopadd {
    padding-left: 0;
    padding-right: 0;
  }
}

/**
 *  Common CSS
 */
.bg-lightred {
  background-color: #fff9f8;
}

.btn {
  background-color: #F98B36;
  border-radius: 0;
  color: #fff;
  display: inline-block;
  font-size: 16px;
  font-weight: 500;
  line-height: 0.8;
  text-transform: uppercase;
  min-width: 170px;
  padding: 20px 15px;
}

.btn1{
  background-color: #F98B36;
  border-radius: 0;
  color: #fff;
  display: inline-block;
  font-size: 16px;
  font-weight: 500;
  line-height: 0.8;
  text-transform: uppercase;
  min-width: 170px;
  padding: 20px 15px;
  text-decoration: none;
}

.btn1:hover{
  background-color: #fff;
  text-decoration: none;
  border: 1px solid #F98B36;
  color: #F98B36;
}

.btn1-round {
  border-radius: 50px;
}

.btn-round {
  border-radius: 50px;
}

.btn-outline {
  background-color: transparent;
  border: 1px solid #fff;
}

.btn:focus {
  box-shadow: none;
}

.btn.icon-left i {
  font-size: 20px;
  line-height: .8;
  margin-right: 10px;
}

.video-btn {
  color: #555;
  font-size: 15px;
  font-weight: 400;
}

.video-btn:hover {
  color: #F98B36;
}

.video-btn i {
  border-radius: 50%;
  background-color: #fcf5ee;
  color: #F98B36;
  display: inline-block;
  line-height: 44px;
  text-align: center;
  margin-right: 10px;
  height: 44px;
  width: 44px;
}

.sec-heading {
  margin-bottom: 70px;
  position: relative;
  text-align: center;
}

.sec-heading img {
  max-width: 84px;
  margin: 0 auto 40px;
}

.sec-heading .tagline {
  color: #888;
  display: block;
  font-size: 20px;
  font-weight: 400;
  line-height: .8;
  text-transform: uppercase;
  margin-bottom: 27px;
}

.sec-heading .sec-title {
  color: #1e2528;
  font-size: 42px;
  margin-bottom: 32px;
}

@media screen and (max-width: 767px) {
  .sec-heading .sec-title {
    font-size: 33px;
  }
}

.sec-heading p {
  color: #888;
  margin-bottom: 0;
}

.header {
  position: relative;
  padding: 15px 0;
}

.header.abs-header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1;
}

.logo {
  display: block;
  max-width: 109px;
}

.primary-menu, .footer .foo-nav {
  position: relative;
}

.primary-menu .mobile-menu, .footer .foo-nav .mobile-menu {
  display: none;
}

@media screen and (max-width: 767px) {
  .primary-menu .mobile-menu, .footer .foo-nav .mobile-menu {
    background: transparent;
    border: none;
    color: #F98B36;
    display: block;
    font-size: 18px;
    position: absolute;
    right: 0;
    top: -30px;
    outline: 0;
  }
}

@media screen and (min-width: 768px) {
  .primary-menu li, .footer .foo-nav li {
    display: inline-block;
  }
}

.primary-menu li a, .footer .foo-nav li a {
  color: #263a41;
  display: block;
  font-size: 14px;
  font-weight: 500;
  text-transform: uppercase;
  padding: 0 20px;
}

@media screen and (max-width: 991px) {
  .primary-menu li a {
    font-size: 13px;
    padding: 0 18px;
  }
}

.primary-menu li a:hover, .footer .foo-nav li a:hover {
  color: #F98B36;
}

@media screen and (max-width: 767px) {
  .primary-menu ul {
    background-color: #fcf5ee;
    /* display: none; */
    text-align: center;
    padding: 10px 0;
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    visibility: hidden;
    -webkit-transition: 300ms;
    transition: 300ms;
    width: 100%;
    z-index: 9;
  }
  .primary-menu ul.active {
    /* display: block; */
    opacity: 1;
    visibility: visible;
  }
  .primary-menu li a {
    padding: 10px 15px;
  }
}

.banner {
  background: url(/static/media/leaf.e7a7a200.png) no-repeat left center/12.5%;
  padding: 12% 0;
}

@media screen and (max-width: 1199px) {
  .banner {
    padding-top: 15%;
  }
}

@media screen and (max-width: 767px) {
  .banner {
    padding-top: 130px;
  }
}

.banner span {
  color: #F98B36;
  display: block;
  font-family: "Rufina", serif;
  letter-spacing: 1.5px;
  text-transform: uppercase;
  margin-bottom: 20px;
}

.banner h2 {
  color: #1e2528;
  font-size: 60px;
  line-height: 75px;
  margin-bottom: 25px;
}

@media screen and (max-width: 1199px) {
  .banner h2 {
    font-size: 49px;
    line-height: 60px;
  }
}

@media screen and (max-width: 479px) {
  .banner h2 {
    font-size: 44px;
    line-height: 54px;
  }
}

.banner p {
  color: #555;
  margin-bottom: 50px;
}

@media screen and (min-width: 480px) {
  .banner .video-btn {
    margin-left: 15px;
  }
}

@media screen and (max-width: 479px) {
  .banner .video-btn {
    display: block;
    margin-top: 30px;
  }
}

.banner .spa-img {
  width: 43.4%;
  position: absolute;
  right: 0;
  top: 0;
}

@media screen and (max-width: 991px) {
  .banner {
    text-align: center;
  }
  .banner .spa-img {
    
  }
}

.about {
  text-align: center;
}

.about [class|=flower] {
  position: absolute;
}

.about .flower-1 {
  left: 16%;
  top: 0;
  max-width: 160px;
}

.about .flower-2 {
  right: 135px;
  top: 50%;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  max-width: 88px;
}

@media screen and (max-width: 991px) {
  .about .flower-2 {
    display: none;
  }
}

.service .service-caro {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.service .single-service {
  flex-basis: 25%;
  position: relative;
}

.service .single-service .service-hover {
  background-color: rgba(245,134, 52, 0.9);
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  text-align: center;
  -webkit-transition: all 300ms ease-in-out;
  transition: all 300ms ease-in-out;
  opacity: 0;
  visibility: hidden;
  height: 100%;
  width: 100%;
}

.service .single-service .service-hover img {
  margin: 0 auto 20px;
  width: 47px;
}

.service .single-service .service-hover span {
  color: #fff;
  display: block;
  font-family: "Rufina", serif;
  font-weight: 700;
}

.service .single-service:hover .service-hover {
  opacity: 1;
  visibility: visible;
}

.procedures {
  position: relative;
}

.blog {
  padding-bottom: 20px;
}

.post {
  border: 1px solid #e3e6ea;
  text-align: center;
  -webkit-transition: all 300ms ease-in-out;
  transition: all 300ms ease-in-out;
  padding: 20px;
  margin-bottom: 30px;
}

.post img {
  margin-bottom: 35px;
}

.post h4 {
  height: 60px;
  margin-bottom: 22px;
}

.post h4, .post h4 a {
  color: #263a41;
  font-size: 22px;
}

.post p {
  margin-bottom: 39px;
}

.post .btn {
  background-color: #30383b;
  margin-bottom: 30px;
  padding: 15px;
}

.post:hover {
  box-shadow: 0 5px 20px #f0f0f0;
}

.post:hover .btn {
  background-color: #F98B36;
  color: #fff;
}

.testimonial .single-tst {
  position: relative;
  text-align: center;
  margin-bottom: 30px;
}

.testimonial .single-tst img {
  margin: 0 auto 45px;
  width: 59px;
}

.testimonial .single-tst p {
  color: #263a41;
  font-family: "Rufina", serif;
  font-size: 18px;
  font-weight: 400;
  line-height: 35px;
  margin-bottom: 43px;
}

.testimonial .single-tst .client-info .thumb {
  border-radius: 50%;
  margin-bottom: 22px;
  height: 76px;
  width: 76px;
}

.testimonial .single-tst .client-info p {
  font-size: 22px;
  margin-bottom: 0;
}

.testimonial .single-tst .client-info p span {
  display: inline-block;
  font-family: "Roboto", sans-serif;
  font-size: 15px;
  font-weight: 300;
}

.testimonial .owl-dots {
  text-align: center;
}

.testimonial .owl-dots span {
  background-color: rgba(245,134, 52, 0.6);
  border-radius: 50%;
  display: inline-block;
  margin: 0 5px;
  height: 8px;
  width: 8px;
}

.appointment .v2 {
  padding-bottom: 0;
  border-bottom: 1px solid rgba(245,134, 52, 0.6);
}

.appointment .appointment-wrap {
  display: flex;
  flex-wrap: wrap;
}

.appointment .appointment-wrap figure {
  flex-basis: 40%;
}

@media screen and (max-width: 1300px) {
  .appointment .appointment-wrap figure {
    display: none;
  }
}

.appointment .appointment-wrap .appointment-form {
  flex-basis: 60%;
  padding-left: 100px;
}

@media screen and (max-width: 1300px) {
  .appointment .appointment-wrap .appointment-form {
    flex-basis: 100%;
    padding: 30px 15px;
  }
}

.appointment .appointment-wrap .appointment-form form {
  max-width: 650px;
  position: relative;
  top: 50%;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
}

@media screen and (max-width: 1300px) {
  .appointment .appointment-wrap .appointment-form form {
    margin: 0 auto;
  }
}

.appointment-form {
  background-color: #fff9f8;
}

.appointment-form .form-field {
  position: relative;
  width: 100%;
}

.appointment-form .form-field input, .appointment-form .form-field textarea, .appointment-form .form-field select {
  background-color: transparent;
  border: none;
  border-bottom: 1px solid #d4d3d3;
  color: #999;
  display: block;
  font-size: 12px;
